.nav {
    display: block;
    height: 70px;
    background-color: gainsboro;
  }
  
  .link {
    display: inline-block;
    text-decoration: none;
    padding: 20px;
    font-weight: 700;
    color: black;
    font-size: 20px;
  }
  
  .activeLink {
    composes: link;
    color: #0d6efd;
  }
  
  .loader {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
  }